/** @format */

import React from 'react';
import { UserProfileForm } from 'components/UserProfileForm/UserProfileForm';

import { FiltersIcon } from 'components/Filter/FiltersBtn.styled';
import { UserIcon } from 'components/UserProfileForm/UserIcon';

import { customStyles } from '../Modal.styled';
import ModalWindow from '../Modal';
import { closeIconStyles, modalTextStyles } from './ModalEditProfileStyled';

export const ModalEditProfile = ({ isModalState, statehangeModal }) => {
	return (
		<ModalWindow
			isOpen={isModalState}
			onRequestClose={statehangeModal}
			style={customStyles}
			contentLabel='onRequestClose'
		>
			<FiltersIcon name='close' onClick={statehangeModal} style={closeIconStyles} />
			<p style={modalTextStyles}>Edit profile</p>
			<UserIcon />

			<UserProfileForm />
		</ModalWindow>
	);
};
